import React, { useState } from 'react';
import Radium, { StyleRoot } from 'radium';
import '../../scss/app.scss';
import slideInLeft from 'react-animations/lib/slide-in-left';
import { Button, Form, FormGroup, Label, Input, Row } from 'reactstrap';
import StringFormatValidation from 'string-format-validation';
import InputMask from 'react-input-mask';


const styles = {
    slideInLeft: {
        animation: 'x 0.7s',
        animationName: Radium.keyframes(slideInLeft, 'slideInLeft')

    }
}

const FormCapture = (props) => {

    const [form, setState] = useState({
        phonenumber: '',
        d1: '',
        d2: '',
        m1: '',
        m2: '',
        domainurl: window.location.host,
        DateOfBirthValid: true,
        btnState: false,
        // 'ssn/dob': '',
        tokenId: 'Ahdh#hh^*',
        // ip: ''
        isEnabled: true

    });

    const validationRules = {
        dob: {
            type: 'number',
            size: 4,
        }
    }

    var dob = form.d1 + form.d2 + form.m1 + form.m2

    const submitInfo = e => {
        e.preventDefault();

        const validateDob = StringFormatValidation.validate(validationRules.dob, dob);


        if (validateDob === false) {
            setState({ DateOfBirthValid: false });
            console.log(form.DateOfBirthValid);
            console.log('DOB INVALID');

        } else {
            console.log('BOTH VALID');
            setState({
                phoneNumberValid: true,
                DateOfBirthValid: true
            });


            //SAVE CAPTURED DATA TO EXTERNAL STATE OR PASS DATA AS PROP

            console.log(props.currentPageCount);
            console.log(form.phonenumber + " | " + dob + " | " + form.domainurl + " | " + form.tokenId);


        }
    };

    const updateField = e => {
        setState({
            [e.target.name]: e.target.value
        });

        if (e.target.name === 'm2') {
            setState({
                isEnabled: false
            });
        } else {
            setState({
                isEnabled: true
            });

        }
    };


    return (
        <StyleRoot>
            <div className="modalformcapture " style={styles.slideInLeft} >

                <div className="getloan ">
                    <h1 >ENTER THE 2 PIECES OF INFORMATION BELOW</h1>
                </div>
                <Form onSubmit={submitInfo}>
                    <div className='center'>
                        <div className='center_div dobAlignTop'>

                            <h4 style={style.generalText}  >10 Digit Mobile Phone Number</h4>

                        </div>

                        <div className='center_div' >
                            <FormGroup>
                                <Input mask="(999) 999-9999" maskChar=" " tag={InputMask} required name='phoneNumber' onChange={updateField} value={form.phoneNumber} />
                            </FormGroup>


                        </div>

                        <div className='center_div dobAlign'>
                            <h4 style={style.generalText} >First 4 Digits of  Your Date of Birth</h4> <br />
                        </div>

                        <div className='center_div'>
                            <Row className='dobAlignRight'>
                                <Input name='d1' style={style.inputTag} onChange={updateField} value={form.d1} required placeholder='D' ></Input>
                                <Input name='d2' style={style.inputTag} onChange={updateField} value={form.d2} required placeholder='D'></Input>
                                <Label className='stroke text-primary'>\</Label>
                                <Input name='m1' style={style.inputTag} onChange={updateField} value={form.m1} required placeholder='M'></Input>
                                <Input name='m2' style={style.inputTag} onChange={updateField} value={form.m2} required placeholder='M'></Input>
                            </Row>

                        </div>

                        {
                            form.DateOfBirthValid === false ? <p className='error text-danger '  >date of birth <b> MUST</b>  be 4 Digits</p> : <p></p>
                        }

                    </div>
                    <hr />
                    {
                        form.DateOfBirthValid === true ? <Button disabled={form.isEnabled} color='primary' className='submit-btn'>Next</Button> : <Button onClick={props.onPageChange} disabled={form.isEnabled} color='primary' className='submit-btn'>Next1</Button>
                    }

                </Form>

            </div>
        </StyleRoot>

    );
}


const style = {
    pad: {
        paddingLeft: 30
    },

    button: {
        color: '#ffffff',
        textAlign: 'center',
        border: 2,
        borderColor: '#ffffff',
        fontSize: 16,

        float: 'right',
        height: 40,
        width: 120,
        borderRadius: 5,
        position: 'absolute',
        right: 10,
        padding: 10,
        margin: 5

    },
    elementHolder: {
        display: 'flex',
    },
    inputTag: {
        width: 40,
        padding: 10,
        borderRadius: 5,
        margin: 3
    },
    selectTag: {
        width: 80,
        padding: 10,
        margin: 3,
        borderRadius: 5
    },
    topText: {
        color: '#000062'
    },
    generalText: {

        color: '#000062',
        textAlign: 'center',
        fontWeight: 500,
    }

}

export default FormCapture;