import React, { useState } from 'react';
import { Col, Row, Button, Form, FormGroup, Label, Input } from 'reactstrap';
import Radium, { StyleRoot } from 'radium';
import '../../scss/app.scss';
import slideInLeft from 'react-animations/lib/slide-in-left';

const styles = {
    slideInLeft: {
        animation: 'x 0.7s',
        animationName: Radium.keyframes(slideInLeft, 'slideInLeft')
    }
}

const ReviewInfo = (props) => {

    const userInfo = {
        name: 'Elisha ',
        lastname: 'BERE',
        dob: '31/05/95',
        street: 'Top dawg street',
        city: 'Compton',
        houseNumber: '2',
        state: 'CA',
        zip: '90059'
    };

    const [data, setData] = useState({
        name: '',
        lastname: '',
        dob: '',
        street: '',
        city: '',
        houseNumber: '',
        state: '',
        zip: ''

    });


    return (
        <StyleRoot>
            <div style={styles.slideInLeft} >


                <div className='getloan'>
                    <h1  >ENTER THE 2 PIECES OF INFORMATION BELOW</h1>
                </div>

                <div className='m-2 modalformcapture'>
                    <Form>
                        <Row form>
                            <Col md={5}>
                                <FormGroup>
                                    <Label for="Name">Name</Label>
                                    <Input type="text" name="name" id="name" defaultValue="Elisha " />
                                </FormGroup>
                            </Col>
                            <Col md={2}>
                                <FormGroup>
                                    <Label for="ML">ML</Label>
                                    <Input type="text" name="ml" id="ml" defaultValue="D" />
                                </FormGroup>
                            </Col>
                            <Col md={5}>
                                <FormGroup>
                                    <Label for="Lastname">Lastname</Label>
                                    <Input type="text" name="lastname" id="lastname" defaultValue='Bere' />
                                </FormGroup>
                            </Col>
                        </Row>
                        <Row form>

                            <Col md={5}>
                                <FormGroup>
                                    <Label for="street">Street Address</Label>
                                    <Input type="text" name="street" id="street" defaultValue="Soloboni ntaa!" />
                                </FormGroup>
                            </Col>
                            <Col md={2}>
                                <FormGroup>
                                    <Label for="Number">#</Label>
                                    <Input type="text" name="number" id="number" defaultValue="2" />
                                </FormGroup>
                            </Col>
                            <Col md={5}>
                                <FormGroup>
                                    <Label for="City">City</Label>
                                    <Input type="text" name="city" id="city" defaultValue="Compton" className='green' />
                                </FormGroup>
                            </Col>
                        </Row>

                        <Row form>
                            <Col md={6}>
                                <FormGroup>
                                    <Label for="Dob">Date of Birth</Label>
                                    <Input
                                        type="date"
                                        name="date"
                                        id="dateofbirth"
                                        placeholder="date placeholder"
                                    />
                                </FormGroup>
                            </Col>
                            <Col md={4}>
                                <FormGroup>
                                    <Label for="State">State</Label>
                                    <Input type="text" name="state" id="State" />
                                </FormGroup>
                            </Col>
                            <Col md={2}>
                                <FormGroup>
                                    <Label for="Zip">Zip</Label>
                                    <Input type="text" name="zip" id="Zip" />
                                </FormGroup>
                            </Col>
                        </Row>

                        <Button onClick={props.onPageChange} className='submit-btn' color='primary'>Submit</Button>
                    </Form>

                </div>
            </div>
        </StyleRoot>
    );
}

export default ReviewInfo;
